@import (less) "./../less/color.less";

:host(.ng-dirty.ng-invalid.nbp-show-validation) {
  .spinner-container {
    background-color: @white;
    border: 2px solid @red;
    border-radius: 2px;
  }
}

.spinner-tooltip-left {
  position: relative;
  left: -12px;
  top: -30px;
}

.spinner-tooltip-right {
  position: relative;
  right: -12px;
  top: -30px;
}

.spinner-container {
  display: inline-flex;
  // width: calc(~'100% - 19px');;
  background-color: @white;
  border: 1px solid @darkest_gray;
  border-radius: 2px;
  // overflow: auto;
  padding: 4px;
  height: 50px;
  // box-shadow: inset 1px 1px 2px 0px;
  &:disabled {
    background-color: @light_gray;
  }
  .spinner-button {
    width: 30px;
    height: 40px;
    background-color: @green;
    // font-size: 24px;
    // line-height: 24px;
    color: white;
    border: none;
    border-radius: 2px;
    padding: 0px;
    padding-bottom: 2px;
    &:disabled {
      // background-color: @light_gray;
      opacity: 0.5;
    }
    &.left {
      float: left;
    }
    &.right {
      float: right;
    }
  }
  .spinner-input {
    width: calc(~'100% - 60px');
    border: none;
    height: 40px;
    text-align: center;
    font-size: 1em;
    color: @darkest_gray;
    //font-weight: bold;
    ::-ms-clear {
      display: none;
    }
    ::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}

.icon-spinner{
  vertical-align: middle;
}

:host-context(.contrasto-on) {
  :host(.ng-dirty.ng-invalid.nbp-show-validation) {
    .spinner-container {
      background-color: @white;
      border-color: @black;
    }
  }
  .spinner-container {
    background-color: @white;
    border-color: @black;
    &:disabled {
      background-color: @black;
      color: @white;
    }
    .spinner-button {
      background-color: @black;
      color: @white;
      padding-bottom: 2px;
      &:disabled {
        background-color: @white;
        color: @black;
      }
    }
    .spinner-input {
      color: @black;
    }
  }
}